#!/usr/bin/env node
/**
 * Visibility Pipeline - Hooks â†’ Backend Integration
 * Logs task completion events to backend for real-time visibility
 */

const http = require('http');
const fs = require('fs').promises;
const path = require('path');
const { safeStringify, sanitizeObject } = require('./safe-json-stringify.cjs');

// BLOCKER-3: Import tagging protocol for WHO/WHEN/PROJECT/WHY metadata
let taggedMemoryStore, taggingAvailable;
try {
  const taggingProtocol = require('./memory-mcp-tagging-protocol.js');
  taggedMemoryStore = taggingProtocol.taggedMemoryStore;
  taggingAvailable = true;
  console.log('[Visibility] Memory MCP tagging protocol loaded');
} catch (err) {
  taggingAvailable = false;
  console.warn('[Visibility] Tagging protocol not available - using basic metadata');
}

// BLOCKER-5 FIX: Environment-configurable backend
const BACKEND_HOST = process.env.BACKEND_HOST || 'localhost';
const BACKEND_PORT = parseInt(process.env.BACKEND_PORT || '8000', 10);
const BACKEND_PROTOCOL = process.env.BACKEND_PROTOCOL || 'http';
const BACKEND_URL = `${BACKEND_PROTOCOL}://${BACKEND_HOST}:${BACKEND_PORT}`;
const LOG_DIR = path.join(__dirname, '../../logs');

// ISSUE #5 FIX: Retry configuration
const MAX_RETRIES = 3;
const INITIAL_BACKOFF_MS = 1000;  // 1s, 2s, 4s

// Production validation
if (process.env.NODE_ENV === 'production' && !process.env.BACKEND_HOST) {
  console.error('[Visibility] FATAL: BACKEND_HOST required in production');
  process.exit(1);
}

console.log(`[Visibility] Backend configured: ${BACKEND_URL}`);

async function ensureLogDir() {
  try {
    await fs.mkdir(LOG_DIR, { recursive: true });
  } catch (err) {
    // Directory already exists
  }
}

async function logToFile(event, data) {
  await ensureLogDir();
  const timestamp = new Date().toISOString();
  const logFile = path.join(LOG_DIR, `visibility-${timestamp.split('T')[0]}.log`);
  const logEntry = JSON.stringify({ timestamp, event, ...data }, null, 2);
  await fs.appendFile(logFile, logEntry + '\n', 'utf8');
}

function generateId() {
  return `${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
}

/**
 * ISSUE #5 FIX: Exponential backoff retry wrapper
 */
async function sendToBackendWithRetry(event, data, attempt = 1) {
  try {
    return await sendToBackend(event, data);
  } catch (error) {
    if (attempt >= MAX_RETRIES) {
      console.error(`[Visibility] Failed after ${MAX_RETRIES} attempts: ${error.message}`);
      // Fallback to file logging
      await logToFile(event, data);
      throw error;
    }

    const backoffMs = INITIAL_BACKOFF_MS * Math.pow(2, attempt - 1);
    console.warn(`[Visibility] Attempt ${attempt}/${MAX_RETRIES} failed, retrying in ${backoffMs}ms...`);

    await new Promise(resolve => setTimeout(resolve, backoffMs));
    return sendToBackendWithRetry(event, data, attempt + 1);
  }
}

async function sendToBackend(event, data) {
  return new Promise((resolve, reject) => {
    const timestamp = new Date().toISOString();

    // BLOCKER-3: Add WHO/WHEN/PROJECT/WHY metadata using tagging protocol
    let enhancedMetadata = data;
    if (taggingAvailable) {
      const agentType = data.agent_type || 'unknown';
      const taggedData = taggedMemoryStore(agentType, JSON.stringify(data), {
        task_id: data.task_id || 'unknown',
        intent: 'implementation',
        description: `Event: ${event}`,
        custom_namespace: 'visibility-pipeline/events'
      });
      enhancedMetadata = {
        ...data,
        tagged_metadata: taggedData.metadata
      };
    }

    // Wrap single event in EventBatch structure (BLOCKER-1 FIX)
    const eventObject = {
      event_type: event,
      timestamp: timestamp,
      event_id: generateId(),
      agent_id: data.agent_id || data.task_id || 'unknown',
      agent_name: data.agent_type || 'unknown',
      agent_role: 'worker',
      operation: event,
      status: 'success',
      metadata: enhancedMetadata
    };

    const batch = {
      events: [eventObject],  // Wrap in array
      batch_id: `batch-${generateId()}`,
      timestamp: timestamp
    };

    // ISSUE #4 FIX: Sanitize batch to prevent prototype pollution
    const sanitizedBatch = sanitizeObject(batch);
    const payload = safeStringify(sanitizedBatch);

    const options = {
      hostname: BACKEND_HOST,
      port: BACKEND_PORT,
      path: '/api/v1/events/ingest',  // Updated endpoint
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Content-Length': Buffer.byteLength(payload)
      },
      timeout: 5000
    };

    const req = http.request(options, (res) => {
      let body = '';
      res.on('data', chunk => body += chunk);
      res.on('end', () => {
        if (res.statusCode >= 200 && res.statusCode < 300) {
          console.log(`[Visibility] Event logged to backend: ${event}`);
          resolve({ success: true, status: res.statusCode });
        } else {
          reject(new Error(`Backend error: ${res.statusCode}`));
        }
      });
    });

    req.on('error', reject);
    req.on('timeout', () => {
      req.destroy();
      reject(new Error('Request timeout'));
    });

    req.write(payload);
    req.end();
  });
}

async function main() {
  const [event, taskId, agentType, durationMs] = process.argv.slice(2);

  if (!event) {
    console.error('Usage: node visibility-pipeline.js <event> <task-id> <agent-type> <duration>');
    process.exit(1);
  }

  const eventData = {
    task_id: taskId || 'unknown',
    agent_type: agentType || 'unknown',
    duration_ms: parseInt(durationMs) || 0,
    source: 'hooks-pipeline'
  };

  try {
    // ISSUE #5 FIX: Use retry wrapper instead of single attempt
    await sendToBackendWithRetry(event, eventData);
  } catch (err) {
    // Already logged to file by retry wrapper
    console.error(`[Visibility] Event delivery failed completely: ${err.message}`);
  }

  console.log(`[Visibility] Event processed: ${event}`);
  process.exit(0);
}

main().catch(err => {
  console.error(`[Visibility] Fatal error: ${err.message}`);
  process.exit(1);
});
